.footer {
  width: 100%;
  background-color: #574B8A;
  padding: 70px 0;
}
.col-footer {
  width: 25%;
  padding: 0 15px;
}
.col-footer h4 {
  font-size: 18px;
  color: #FFFFFF;
  text-transform: capitalize;
  margin-bottom: 30px;
  font-weight: 500;
  position: relative;
}
.col-footer h4::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 2px;
  background-color: #E8B900;
  box-sizing: border-box;
  width: 50px;
}
.col-footer ul li:not(:last-child) {
  margin-bottom: 10px;
}
.col-footer ul li a {
  font-size: 16px;
  text-transform: capitalize;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 300;
  display: block;
  transition: all 0.3s ease;
}
.col-footer ul li a:hover {
  padding-left: 10px;
}
.col-footer .social a {
  display: inline-block;
  height: 40px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  margin-right: 0 10px 10px;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  color: #FFFFFF;
  transition: all 0.5s ease;
}
.col-footer .social a:hover {
  color: #E8B900;
  background-color: #FFFFFF;
}
@media(max-width:767px) {
  .col-footer {
    width: 100%;
    margin-top: 30px;
  }
  .row-footer {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .col-footer h4::before {
    display: none;
  }
}
